import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useState } from 'react';
import { MonthView } from './MonthView';

export const CalendarView = ({ year, month, onCellClick }) => {
  const [currentYear, setCurrentYear] = useState(year);
  const [firstDayOfTheMonth, setFirstDayOfTheMonth] = useState(
    moment(year.concat('-').concat(month)).startOf('month'),
  );
  const [lastDayOfTheMonth, setLastDayOfTheMonth] = useState(
    moment(year.concat('-').concat(month)).endOf('month'),
  );
  const [allMonths, setAllMonths] = useState(moment.months());
  const [currentMonth, setCurrentMonth] = useState(firstDayOfTheMonth.format('MMMM'));

  const handleMonthChange = newMonth => {
    setCurrentMonth(newMonth);
  };

  return (
    <div className="Container">
      <div className="row">
        <h3 className="text-center">{currentYear}</h3>
      </div>
      <div className="row">
        <MonthView
          firstDay={firstDayOfTheMonth}
          endDay={lastDayOfTheMonth}
          onCellClick={onCellClick}
        ></MonthView>
      </div>
      <div className="row">
        <ul className="list-group list-group-horizontal">
          {allMonths.map(m => {
            console.log(currentMonth);
            return (
              <li
                className={m == currentMonth ? 'list-group-item active' : 'list-group-item'}
                onClick={e => handleMonthChange(e.target.dataset.value)}
                data-value={m}
              >
                {m}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
